'use strict';

// MODULES //

import defineProperty from '@stdlib/utils-define-property';


// MAIN //

/**
* Defines a configurable read-write accessor.
*
* @param {Object} obj - object on which to define the property
* @param {(string|symbol)} prop - property name
* @param {Function} getter - get accessor
* @param {Function} setter - set accessor
*
* @example
* var name = 'bar';
* var obj = {};
*
* function getter() {
*     return name + ' foo';
* }
*
* function setter( v ) {
*     name = v;
* }
*
* setConfigurableReadWriteAccessor( obj, 'foo', getter, setter );
*
* var v = obj.foo;
* // returns 'bar foo'
*
* obj.foo = 'beep';
*
* v = obj.foo;
* // returns 'beep foo'
*/
function setConfigurableReadWriteAccessor( obj, prop, getter, setter ) { // eslint-disable-line id-length
	defineProperty( obj, prop, {
		'configurable': true,
		'enumerable': true,
		'get': getter,
		'set': setter
	});
}


// EXPORTS //

export default setConfigurableReadWriteAccessor;
